.swiper-container {
  width: 100%;
  height: 330px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  width: 420px !important;
  background: #fff;
  padding: 50px !important;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  padding-left: 80px;
}



.titleview {
  font-family: Inter;
  font-size: 18px;
  font-weight: 400px;
}

.titletext {
  font-family: Inter;
  font-size: 14px;
  font-weight: 400px;
  line-height: 26px;
}

.swiper-pagination-bullet-active {
  color: black !important;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  color: rgb(0, 0, 0, 0.3);
  display: none;
}
